import {
	mapState
} from 'vuex';
import imageLoader from '@/components/imageLoader.vue'
export default {
	components: {
		imageLoader
	},
	data() {
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: false,
			autoplay: true,
			interval: 5000,
			duration: 500,
			circular: true,
			dataObj: {}
		};
	},
	onNavigationBarButtonTap(){
		console.log(11122)
		this.selectArea();
	},
	onNavigationBarSearchInputClicked(){
		console.log(223)
		this.goDetail(1);
	},
	onLoad() {
		var _this = this;
		this.$http.request({
			url: _this.$api.HomePicture,
			method: 'POST'
		}).then(res => {
			console.log(res)
			this.dataObj = res.data;
		})
	},
	computed: {
		...mapState(['myLocation'])
	},
	methods: {
		goDetail(index) {
			console.log(1)
			let url = index == 1 ? "../../product/productCenter/productCenter" : '';
			uni.navigateTo({
				url: url
			})
		},
		selectArea() {
			uni.navigateTo({
				url: '../areaSelect/areaSelect'
			})
		}
	}
}
